import { FC, ReactNode } from 'react'
import Link from 'next/link'
import { LidoLogo } from '@lidofinance/lido-ui'

import { HeaderActions } from './components/headerActions'
import { Navigation } from './components/navigation'
import { NavigationLinkProps } from './components/navigation/components/navigationLink'
import { HeaderStyle, HeaderLogoStyle } from './styles'

export type HeaderProps = {
  headerActions: ReactNode
  // FIXME: something went wrong and optional field not working without undefined
  navigation?: NavigationLinkProps[] | undefined
}

export const Header: FC<HeaderProps> = ({ navigation, headerActions, ...rest }) => (
  <HeaderStyle {...rest} size="full" forwardedAs="header">
    <HeaderLogoStyle>
      <Link href="/" passHref legacyBehavior>
        <LidoLogo />
      </Link>
    </HeaderLogoStyle>
    <Navigation links={navigation} />
    <HeaderActions leftSlot={headerActions} />
  </HeaderStyle>
)
