import React, { FC, memo } from 'react'

import { NavigationLink, NavigationLinkProps } from './components/navigationLink'
import { NavigationStyle } from './styles'

export type NavigationProps = {
  // FIXME: something went wrong and optional field not working without undefined
  links?: NavigationLinkProps[] | undefined
}

export const Navigation: FC<NavigationProps> = memo(({ links, ...rest }) => {
  if (!links) {
    return null
  }

  return (
    <NavigationStyle {...rest}>
      {links.map((pageProps: NavigationLinkProps) => (
        <NavigationLink key={pageProps.href} {...pageProps} />
      ))}
    </NavigationStyle>
  )
})

Navigation.displayName = 'Navigation'
